.quiz-container {
    display: flex;
    min-height: 500px;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    flex-direction: column;
    justify-content: center;
  }

  .quizContainerC{
    background-color: white;
    padding: 2em;
    border-radius: 0.5em;
  }
  
  .quiz {
    display: flex;
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    border: 2px solid #ccc;
    border-radius: 0.5em;
    flex-direction: column;
    padding: 50px;
  }
  .imageQuestion{
    width: 30vh;
    height: 40vh;
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .recommencer-button {
    font-size: 16px;
    font-weight: bold;
    padding: 10px 20px;
    margin-bottom: 10px;
    margin-top: 50px;
    background-color: #4CAF50;
    color: white;
    border: 2px solid #4CAF50;
    border-radius: 8px;
    cursor: pointer;
    font-family: Arial, sans-serif;
    position: relative;
  }
  
  
  .recommencer-button:hover {
    background-color: #3c893d;
    border-color: #3c893d;
  }
  
  .quiz-section {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    justify-content: space-between;
    flex-direction: column-reverse;
  }
  
  .quiz-count {
    font-size: 20px;
    font-weight: bold;
  }
  
  .quiz-text {
    font-size: 24px;
    font-weight: bold;
  }
  
  .answer-section {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    flex-direction: column;
  }
  .answer-section button.wrong {
    background-color: red;
    color: white;
  }
  .listReponsesFauses{
    margin-top:20px;
    padding: 10px;
    background-color: white;
    border-radius: 0.5em;
    border: solid black 5px;
    text-decoration: none;
  }
  li{
    list-style-type: none;
    border: solid black 5px;
  }
  

  button {
    font-size: 16px;
    font-weight: bold;
    padding: 10px 20px;
    margin-bottom: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #3e8e41;
  }
  